html {
    background: linear-gradient(35deg, #43cea2, #185a9d);
    background-size: cover;
    width: 100%;
    height: 100%;
    text-align: center;

}
#root{
    margin: auto;
    height: 100vh;
    align-items: center;
    display: flex;
}
.App {

    display: flex;
    justify-content: center;
    margin: auto;
}

.counter {
    height: auto;
    border: 2px solid white;
    border-radius: 10px;
}


.option {
    font-size: 30px;
}

.up {
    margin: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    font-size: 60px;
    font-weight: 800;
    color: antiquewhite;
    background-color: mediumaquamarine;
}

.upOff {
    margin: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    font-size: 30px;
    font-weight: 800;
    color: antiquewhite;
    background-color: mediumaquamarine;
}

.down {
    margin: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    background-color: mediumaquamarine;
}

.downOff {
    margin: 20px;
    padding: 20px;
    border: 2px solid white;
    border-radius: 10px;
    background-color: mediumaquamarine;
    color: red;
    opacity: 0.5;
    font-size: 30px;
}

button {
    display: inline-block;
    margin: 5px;
    border: 2px solid white;
    border-radius: 10px;
    height: 40px;
    min-width: 90px;
    font-weight: 600;
    color: white;
    background-color: cadetblue;
    outline: none;
    font-size: 30px;
}

button:hover {
    background-color: blue;
}

.button1 {
    display: inline-block;
    margin: 10px;
    border: 2px solid white;
    border-radius: 10px;
    height: 30px;
    min-width: 80px;
    font-weight: 600;
    color: white;
    background-color: cadetblue;
}

.color {
    color: red;
}

.colorOff {
    color: red;
    opacity: 0.5;
}

div button.sum {
    height: 30px;
    max-width: 30px;
}

input {
    margin-left: 40px;
    width: 60px;
    outline: none;
    padding-left: 30px;
}

@media screen and (max-width: 450px) {
    .App {
        margin: 30px;
    }

    .counter {
        width: 300px;
        justify-content: center;
    }

    input {
        margin-left: 20px;
        width: 30px;
    }
}
@media screen and (max-width: 750px) {
    .App {
        margin: 30px;
    }


    input {
        margin-left: 20px;
        width: 30px;
    }
}











